/* Minimal design for Bekali Aslonov's personal website */
/* Based on the provided template with clean typography and philosophical content */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Helvetica Neue', Arial, sans-serif;
  line-height: 1.6;
  color: #333;
  background: #fff;
  padding: 40px 20px;
  max-width: 720px;
  margin: 0 auto;
}

h1 {
  font-size: 32px;
  font-weight: 600;
  margin-bottom: 32px;
  color: #000;
  letter-spacing: -0.5px;
}

h2 {
  font-size: 18px;
  font-weight: 600;
  margin-top: 36px;
  margin-bottom: 16px;
  color: #000;
}

ul {
  list-style: none;
  padding-left: 0;
}

li {
  margin-bottom: 8px;
  padding-left: 20px;
  position: relative;
}

li:before {
  content: "â€¢";
  position: absolute;
  left: 0;
  color: #666;
}

ul ul {
  margin-top: 6px;
  margin-left: 20px;
  font-size: 15px;
  color: #555;
}

ul ul li {
  margin-bottom: 4px;
}

.quote {
  font-style: italic;
  color: #555;
  border-left: 2px solid #ddd;
  padding-left: 16px;
  margin: 16px 0;
  font-size: 15px;
}

strong {
  font-weight: 500;
  color: #000;
}

a {
  color: #0066cc;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.section {
  margin-bottom: 40px;
}

.tagline {
  font-size: 16px;
  color: #666;
  margin-top: -24px;
  margin-bottom: 32px;
}

/* Responsive design */
@media (max-width: 768px) {
  body {
    padding: 20px 16px;
  }
  
  h1 {
    font-size: 28px;
  }
  
  h2 {
    font-size: 16px;
    margin-top: 32px;
  }
}

@media (max-width: 480px) {
  body {
    padding: 16px 12px;
  }
  
  h1 {
    font-size: 24px;
  }
  
  h2 {
    font-size: 15px;
  }
  
  .tagline {
    font-size: 15px;
  }
}